.button {
  display: inline-block;
  padding: 10px 20px;
  border-radius: 4px;
  border: 1px solid #ccc;
  color: #000;
  margin-right: 15px;
  margin-top: 10px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 12px;
  cursor: pointer;
}

.button:focus {
  outline: none;
}

.button:hover {
  background: rgba(255, 255, 255, .2);
}

.button:active {
  background: rgba(55, 55, 55, .1);
}

.button:disabled {
  background: #ccc;
  color: #000;
  cursor: not-allowed;
}

.error {
  background: rgba(240, 87, 140, 1);
}

.success {
  background: rgba(161, 240, 69, .7);
}

.primary {
  background: #124F86;
  color: #fff;
}